<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="container-ca">
      <div class="login-cainfo">
        <div class="ca-icon"><van-image :src="imageList.img1" fit="cover" width="100%" height="100%" /></div>
        <div class="ca-text">移动证书下载</div>
      </div>
      <div class="login-form">
        <van-cell-group>
          <div class="input-item">
            <van-field
              ref="name"
              v-model="orderNo"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入测试订单号"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
          </div>
          <div class="input-item">
            <van-field
              ref="name"
              v-model="PIN1"
              v-validate="'required|mmLength'"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入移动证书Pin码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
          </div>

          <div class="input-item">
            <van-field
              ref="name"
              v-model="PIN2"
              v-validate="'required|mmLength'"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入移动证书Pin码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
          </div>
        </van-cell-group>
        <van-notice-bar
          style="margin-top:15px;"
          left-icon="info-o"
          text="此密码将作为您以后的移动证书登录密码,请妥善保管！"
        />
        <div class="login-button">
          <van-button round type="info" @click="goXiaZai()">下载证书</van-button>
        </div>
      </div>
    </div>
    <!-- 证书安装成功弹窗 begin -->
    <van-popup v-model="show" closeable round style="width:90%;height:320px;">
      <div class="information-tips">
        <div class="tips-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.tipsimg1" /></div>
        <div class="information-tips-tit">CA移动证书下载成功</div>
        <div class="information-tips-txt">CA移动证书下载成功</div>

        <div class="information-tips-btn">
          <ul>
            <li><van-button plain type="info" size="large" round @click="showPopup()">取消</van-button></li>
            <li><van-button type="info" size="large" round @click="goLogin()">立即登录</van-button></li>
          </ul>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter, NativeCaJS, NativeJS } from '@/common/js/mixin.js'
import { saveSearch, loadSearch } from '@/common/js/catch'
import { CALoad } from '@/store/module.js'

export default {
  name: 'CaLoginXx',
  components: {
    headerBar
  },
  mixins: [pageRouter, NativeCaJS, NativeJS],
  data() {
    return {
      eyesChange2: false,
      eyesChange1: false,
      PIN2: '',
      PIN1: '',
      orderNo: '',
      headerData: {
        title: '移动证书下载',
        leftUrl: 'back',
        leftText: ' '
      },
      imageList: {
        img1: require('@/assets/images/login/icon-ca.png'),
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      },
      show: false, // 暂时弹出框
      importStateData: false, // 是否导入成功
      certInfo: {}, // 暂存数据
      caListItem: [],
      caListPin: []
    }
  },
  watch: {
    // ca 下载
    Native_CaLoad(val) {
      console.info('Native_CaLoad===0==', val)
      if (val === 'true') {
        console.info('Native_CaLoad===1==', val)
      }
    },
    importStateData(val) {
      console.info('importStateData===0==', val)
      if (val) {
        saveSearch('caListPin', this.PIN1)
        saveSearch('caListItem', JSON.stringify(this.certInfo))
        // const count = loadSearch('caListItem').length - 1
        // this.$store.commit('ca/SET_CaNUM', count)
        this.goLoginXx()
        console.info('loadSearch===1==', loadSearch('caListPin'))
        console.info('loadSearch===2==', loadSearch('caListItem'))
        // this.showPopup()
      }
    }

  },

  methods: {
    // 控制是否展示密码
    showEye1() {
      this.eyesChange1 = !this.eyesChange1
    },
    showEye2() {
      this.eyesChange2 = !this.eyesChange2
    },
    // 下载
    goScan() {
      // this.NativeScan()
      this.goXiaZai()
    },
    // 下载
    goLoginXx() {
      this.$router.push({
        name: 'CaLoginXx'
      })
    },
    loadCA(p10) {
      CALoad.data = { 'orderNo': this.orderNo, 'p10': p10 }
      this.asyncApiModule(CALoad).then(res => {
        if (res === '' || res === null || res === undefined) {
          this.$toast('请求失败')
          return
        }
        res = JSON.parse(res)
        console.log('res==1===', res.certInfo)
        if (res.certInfo === '' || res.certInfo === null || res.certInfo === undefined) {
          this.$toast(res.resMsg)
          return
        }

        this.certInfo = res.certInfo

        const ca = { 'strSignCert': res.certInfo.signCert,
          'strEncryptCert': res.certInfo.encCert,
          'strPri': res.certInfo.encPriKey,
          'certInfo': res.certInfo
        }
        this.importDoubleCertificate(JSON.stringify(ca))
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },
    // 下载
    goXiaZai() {
      this.$validator.validate().then(result => {
        console.info('result=====', result)
        if (result) {
          this.getP10()
        }
      })
      // this.NativeLoadCA()
    },
    getP10() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          // return window.myapp.getP10('111111')
          const p10 = window.myapp.getP10('111111')
          if (p10 !== '') {
            this.loadCA(p10)
          }
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getP10', '111111', (data) => {
          const signCert = JSON.parse(data)
          this.loadCA(signCert.P10)
          console.log('ios ==data======', signCert.P10)
        })
      } else {
        this.$toast('返回原生页面')
      }
    },

    importDoubleCertificate(data) {
      if (process.env.NODE_ENV !== 'development') {
        try {
          this.importStateData = window.myapp.importDoubleCertificate(data)
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('importDoubleCertificate', data, (data) => {
          this.importStateData = data
        })
      } else {
        this.$toast('返回原生页面')
      }
    },
    showPopup() {
      this.show = !this.show
    },
    goLogin() {

    }
  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
